.steps
    border: 1px solid $border-color
    border-radius: 12px
    display: flex
    
    &__header
        display: flex
        flex-direction: column
        padding-top: 32px
        padding-bottom: 32px
        padding-right: 32px
        padding-left: 32px
        width: 260px
        flex-shrink: 0
        row-gap: 24px
        border-right: 1px solid $border-color 
    

    &__body
        padding: 32px
        padding-bottom: 40px

        &--md
            max-width: 760px
            flex-grow: 1
            margin-left: auto
            margin-right: auto

        .form-group
            margin-bottom: 16px

        .form-group:last-child
            margin-bottom: 0

        .form-group + .checkbox-field
            margin-top: 24px 

        .checkbox-field
            margin-bottom: 16px

        .result-list
            padding-bottom: 24px
            padding-top: 0
            border-top: none

    &__body-title
        margin-bottom: 24px

    &__body-row
        padding-bottom: 24px
        border-bottom: 1px solid $border-color
        margin-bottom: 24px

    &__body-result
        .result-list
            padding-top: 24px
            padding-bottom: 0
            border-top: 1px solid $border-color

    &__nav
        padding-top: 32px   
        display: flex


    &__nav-right
        display: flex
        column-gap: 16px
        margin-left: auto

    &__date
        display: flex
        margin-top: 48px
        justify-content: flex-end

    &__date-title
        color: $secondary-text

    &__date-data
        display: flex
        padding-left: 20px

    &__date-day
        margin-right: 8px

    &__date-time
        margin-right: 8px