.tag
    display: inline-flex
    align-items: center
    padding-left: 6px
    padding-right: 10px
    padding-top: 5px
    padding-bottom: 5px
    border-radius: 8px
    border: 1px solid $border-color
    font-family: $semibold
    font-size: 16px
    line-height: 24px
    column-gap: 8px
    white-space: nowrap
    

    &__image
        width: 24px
        height: 24px

    &__secondary
        color: $secondary-text

    &--value
        color: $accent
        border: none
        padding: 0